@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap');

$lightColor1: #fef6e4;
$lightColor2: #172c66;
$lightColor3: #ff8e3c;
$lightColor4: lightblue;
$darkColor1: #16161a;
$darkColor2: #965abe;
$darkColor3: #fffffe;
$darkColor4: #b8c1ec;

*{
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
   
   
   
}

//-----------------------------------------------splitter section
.split {
    display: flex;
    flex-direction: row;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    
   
 
}
.gutter {
    background-color:$lightColor1;

    background-repeat: no-repeat;
    background-position: 50%;
}

.gutter.gutter-horizontal {
    
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');
    cursor: col-resize;
}

//--------------------------------------------------------no notes container\


.container-without-notes{
    display: flex;
    width: 100%;
    margin: auto;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color:$lightColor1;;
    color: $lightColor2;
    .container-without-notes-title{
        padding-left: 70px;
        padding-right: 70px;
        padding-bottom: 20px;
    }
}

.no-notes-button{
    cursor: pointer;
    font-size: 60px;
    transition: 350ms;
    color: $lightColor2;
   

    &:hover{
        transition: 350ms;
        color: $lightColor3;
    }
}
//----------------------------------------------------- sidebar section


.sidebar{
    user-select: none;
   display: inline-block;
   flex-direction: column;
   width: 100%;
   background-color:$lightColor1;
   
   

   .sidebar-content{
    overflow-x: hidden;
    height: 83vh;
    overflow-y: auto;
    
   }
  
}

.sidebar-dark{
    @extend .sidebar;
    background-color:$darkColor1;
}

.sidebar-title{
    font-size: 22px;
    display: flex;
    justify-content: space-around;
    background-color:$lightColor1;
    align-items: center;
    height: 13.5vh;
    color:$lightColor2;
    
}

.sidebar-title-dark{
    @extend .sidebar-title;
    background-color:$darkColor1;
    color:$darkColor3;

}

.rendered-block{
    display: grid;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-template-columns: 85% 0% 0% 5% 10%;
    overflow: wrap;
    padding-block: 25px;
    cursor: pointer;
    
    
    
}
.rendered-block-dark{
    @extend .rendered-block;
    background-color:$darkColor1;
    color: $darkColor3;
}

.rendered-block-active{
    border-radius: 7px;
    background-color: $lightColor3;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-template-columns: 85% 0% 0% 5% 10%;
    overflow: wrap;
    padding-block: 25px;
    cursor: pointer;
   
    
}

.rendered-block-active-dark{
    @extend .rendered-block-active;
    background-color:$darkColor2;
    color: $darkColor3;
    
}


.title-date{
   
    padding-left: 10px;
    background-color:transparent;
    color:$lightColor2;

    .title{
        font-size: medium;
        font-weight: 700;
        background-color:transparent;
        color: $lightColor2;
        overflow-wrap: break-word;

    }

    .date{
        background-color:transparent;
        color: $lightColor2;
        font-size: small;

    }
}
.title-date-dark{
   @extend .title-date;

   .title{
    color: $darkColor3;
   }
   .date{
    color: $darkColor3;
   }

}

.title-dark{
    color: $darkColor3;
}



.add-icon{
    color:$lightColor2;
    font-size: 34px;
    cursor: pointer;
    transition: 300ms;
    

    &:hover{
        transform: rotate(90deg);
        transition: 300ms;
        color: $lightColor3;
    }

    &:active{
        color:$lightColor2;
        transition: 300ms;
    }
   
    
}

.add-icon-dark {
    @extend .add-icon;
    color:$darkColor2;
}


.delete-icon-container{
  padding-top: 10px;
  cursor: pointer;

}

.delete-icon{
    cursor: pointer;
    background-color: transparent;
    padding-right: 2px;
    color: $lightColor2;
    font-size: 28px;
    cursor: pointer;
    transition: 300ms;
   
    

    &:hover{
        
        transition: 300ms;
        color: #d60707;
    }
}
.delete-icon-dark{
    @extend .delete-icon;
    color: $darkColor3;
}










//--------------------------------------------- editor section
.not-active-note{
    background-color:$lightColor1;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    
}

.not-active-note-dark{
    @extend .not-active-note;
    background-color:$darkColor1;
    color: $darkColor3;
}




.editor-main{
    display: flex;
    background-color:$lightColor1;
    flex-direction: column;
    padding-right: 5px;
    padding-top: 30px;
    width: 100%;
    overflow: hidden;
   
    padding-left: 4px;
}

.editor-main-dark{
    @extend .editor-main;
    background-color:$darkColor1;
    color: $darkColor3;

}


.small-title{
    padding-top: 4em;
}
.editor-title{
    display: flex;
    justify-content: left;
    background-color:$lightColor2;
    border-radius: 7px;
    color: $lightColor1;
    border: 7px;
    font-size: 18px;
    font-weight: 600;
    width: 90%;
    padding-left: 15px;
    margin-right: 100px;
    padding-block: 13px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    &:focus{
    border: 0px solid $lightColor1;
       background-color: lightblue;
       color: $lightColor2;
       outline: none;
    }
    
}

.editor-title-dark{
    @extend .editor-title;
    background-color:$darkColor2;
    color: $darkColor3;
    &:focus{
        background-color:$darkColor4;
    }

}

.editor-textarea{
    background-color: $lightColor1;
    border-radius: 7px;
    color: $lightColor2;
    border: 7px;
    font-size: 20px;
    font-weight: 600;
    width: 90%;
    padding-left: 15px;
    padding-block: 13px;
    height: 30vh;
    font-size: small;
    font-weight: 500;

    &:focus{
        border: 0px solid black;
       background-color: lightblue;
       color: $lightColor2;
       outline: none;
       font-size: small;
    }

   
    &::placeholder{
        color:#252525a1;
        font-size: small;
    }
    
}

.editor-textarea-dark{
    @extend .editor-textarea;
    background-color: $darkColor1;
    color: $darkColor3;

    &::placeholder{
        color:$darkColor4;
        font-size: small;
    }

    &:focus{
        border: 0px solid black;
       background-color:$darkColor4;
       color:$darkColor1;
       outline: none;
       font-size: small;
    }

}

//------------------------------------------------------toggle dark/light button css


.toggle {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 62px;
    height: 32px;
    display: inline-block;
    position: relative;
    border-radius: 50px;
    overflow: hidden;
    outline: none;
    border: none;
    cursor: pointer;
    background-color:$darkColor4;
    transition: background-color ease 0.3s;
  }
  
  .toggle::after {
    content: "Dark Light";
    display: block;
    position: absolute;
    width: 28px;
    height: 28px;
    background: $darkColor2;
    left: 2px;
    top: 2px;
    border-radius: 50%;
    font: 7px/28px sans-serif;
    text-transform: uppercase;
    font-weight:200 ;
    text-indent: -22px;
    word-spacing: 37px;
    white-space: nowrap;
    box-shadow: 0 1px 2px $darkColor1;
    transition: all cubic-bezier(0.3, 1.5, 0.7, 1) 0.3s;
  }
  
  .toggle:checked {
    background-color: $darkColor4;
  }
  
  .toggle:checked::after {
    left: 32px;
  }

  //----------------------------------------mobile

  @media screen and (max-width: 450px){

    .sidebar-title{
        display: block;
        padding-top: 20px;
        text-align: center;
    }


  }