@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap");
* {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

.split {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}

.gutter {
  background-color: #fef6e4;
  background-repeat: no-repeat;
  background-position: 50%;
}

.gutter.gutter-horizontal {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==");
  cursor: col-resize;
}

.container-without-notes {
  display: flex;
  width: 100%;
  margin: auto;
  text-align: center;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: #fef6e4;
  color: #172c66;
}
.container-without-notes .container-without-notes-title {
  padding-left: 70px;
  padding-right: 70px;
  padding-bottom: 20px;
}

.no-notes-button {
  cursor: pointer;
  font-size: 60px;
  transition: 350ms;
  color: #172c66;
}
.no-notes-button:hover {
  transition: 350ms;
  color: #ff8e3c;
}

.sidebar, .sidebar-dark {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  flex-direction: column;
  width: 100%;
  background-color: #fef6e4;
}
.sidebar .sidebar-content, .sidebar-dark .sidebar-content {
  overflow-x: hidden;
  height: 83vh;
  overflow-y: auto;
}

.sidebar-dark {
  background-color: #16161a;
}

.sidebar-title, .sidebar-title-dark {
  font-size: 22px;
  display: flex;
  justify-content: space-around;
  background-color: #fef6e4;
  align-items: center;
  height: 13.5vh;
  color: #172c66;
}

.sidebar-title-dark {
  background-color: #16161a;
  color: #fffffe;
}

.rendered-block, .rendered-block-dark {
  display: grid;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-template-columns: 85% 0% 0% 5% 10%;
  overflow: wrap;
  padding-block: 25px;
  cursor: pointer;
}

.rendered-block-dark {
  background-color: #16161a;
  color: #fffffe;
}

.rendered-block-active, .rendered-block-active-dark {
  border-radius: 7px;
  background-color: #ff8e3c;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-template-columns: 85% 0% 0% 5% 10%;
  overflow: wrap;
  padding-block: 25px;
  cursor: pointer;
}

.rendered-block-active-dark {
  background-color: #965abe;
  color: #fffffe;
}

.title-date, .title-date-dark {
  padding-left: 10px;
  background-color: transparent;
  color: #172c66;
}
.title-date .title, .title-date-dark .title {
  font-size: medium;
  font-weight: 700;
  background-color: transparent;
  color: #172c66;
  overflow-wrap: break-word;
}
.title-date .date, .title-date-dark .date {
  background-color: transparent;
  color: #172c66;
  font-size: small;
}

.title-date-dark .title {
  color: #fffffe;
}
.title-date-dark .date {
  color: #fffffe;
}

.title-dark {
  color: #fffffe;
}

.add-icon, .add-icon-dark {
  color: #172c66;
  font-size: 34px;
  cursor: pointer;
  transition: 300ms;
}
.add-icon:hover, .add-icon-dark:hover {
  transform: rotate(90deg);
  transition: 300ms;
  color: #ff8e3c;
}
.add-icon:active, .add-icon-dark:active {
  color: #172c66;
  transition: 300ms;
}

.add-icon-dark {
  color: #965abe;
}

.delete-icon-container {
  padding-top: 10px;
  cursor: pointer;
}

.delete-icon, .delete-icon-dark {
  cursor: pointer;
  background-color: transparent;
  padding-right: 2px;
  color: #172c66;
  font-size: 28px;
  cursor: pointer;
  transition: 300ms;
}
.delete-icon:hover, .delete-icon-dark:hover {
  transition: 300ms;
  color: #d60707;
}

.delete-icon-dark {
  color: #fffffe;
}

.not-active-note, .not-active-note-dark {
  background-color: #fef6e4;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}

.not-active-note-dark {
  background-color: #16161a;
  color: #fffffe;
}

.editor-main, .editor-main-dark {
  display: flex;
  background-color: #fef6e4;
  flex-direction: column;
  padding-right: 5px;
  padding-top: 30px;
  width: 100%;
  overflow: hidden;
  padding-left: 4px;
}

.editor-main-dark {
  background-color: #16161a;
  color: #fffffe;
}

.small-title {
  padding-top: 4em;
}

.editor-title, .editor-title-dark {
  display: flex;
  justify-content: left;
  background-color: #172c66;
  border-radius: 7px;
  color: #fef6e4;
  border: 7px;
  font-size: 18px;
  font-weight: 600;
  width: 90%;
  padding-left: 15px;
  margin-right: 100px;
  padding-block: 13px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.editor-title:focus, .editor-title-dark:focus {
  border: 0px solid #fef6e4;
  background-color: lightblue;
  color: #172c66;
  outline: none;
}

.editor-title-dark {
  background-color: #965abe;
  color: #fffffe;
}
.editor-title-dark:focus {
  background-color: #b8c1ec;
}

.editor-textarea, .editor-textarea-dark {
  background-color: #fef6e4;
  border-radius: 7px;
  color: #172c66;
  border: 7px;
  font-size: 20px;
  font-weight: 600;
  width: 90%;
  padding-left: 15px;
  padding-block: 13px;
  height: 30vh;
  font-size: small;
  font-weight: 500;
}
.editor-textarea:focus, .editor-textarea-dark:focus {
  border: 0px solid black;
  background-color: lightblue;
  color: #172c66;
  outline: none;
  font-size: small;
}
.editor-textarea::-moz-placeholder, .editor-textarea-dark::-moz-placeholder {
  color: rgba(37, 37, 37, 0.631372549);
  font-size: small;
}
.editor-textarea:-ms-input-placeholder, .editor-textarea-dark:-ms-input-placeholder {
  color: rgba(37, 37, 37, 0.631372549);
  font-size: small;
}
.editor-textarea::placeholder, .editor-textarea-dark::placeholder {
  color: rgba(37, 37, 37, 0.631372549);
  font-size: small;
}

.editor-textarea-dark {
  background-color: #16161a;
  color: #fffffe;
}
.editor-textarea-dark::-moz-placeholder {
  color: #b8c1ec;
  font-size: small;
}
.editor-textarea-dark:-ms-input-placeholder {
  color: #b8c1ec;
  font-size: small;
}
.editor-textarea-dark::placeholder {
  color: #b8c1ec;
  font-size: small;
}
.editor-textarea-dark:focus {
  border: 0px solid black;
  background-color: #b8c1ec;
  color: #16161a;
  outline: none;
  font-size: small;
}

.toggle {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 62px;
  height: 32px;
  display: inline-block;
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #b8c1ec;
  transition: background-color ease 0.3s;
}

.toggle::after {
  content: "Dark Light";
  display: block;
  position: absolute;
  width: 28px;
  height: 28px;
  background: #965abe;
  left: 2px;
  top: 2px;
  border-radius: 50%;
  font: 7px/28px sans-serif;
  text-transform: uppercase;
  font-weight: 200;
  text-indent: -22px;
  word-spacing: 37px;
  white-space: nowrap;
  box-shadow: 0 1px 2px #16161a;
  transition: all cubic-bezier(0.3, 1.5, 0.7, 1) 0.3s;
}

.toggle:checked {
  background-color: #b8c1ec;
}

.toggle:checked::after {
  left: 32px;
}

@media screen and (max-width: 450px) {
  .sidebar-title, .sidebar-title-dark {
    display: block;
    padding-top: 20px;
    text-align: center;
  }
}/*# sourceMappingURL=styles.css.map */